/*  base  */
* {
  padding: 0;
  margin: 0; }

*, *::after, *::before {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem; }

a {
  text-decoration: none; }

body {
  background-color: #150129;
  color: #fff; }

::-webkit-scrollbar {
  width: 6px;
  animation-name: bar;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  max-height: 2rem !important;
  height: 5px; }

::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 7px; }

@keyframes bar {
  0% {
    background-color: rgba(255, 255, 255, 0.14); }
  50% {
    background-color: red; }
  100% {
    background-color: rgba(255, 255, 255, 0.14); } }

/*  components  */
.drawer {
  position: relative;
  height: calc(100vh - 1px);
  box-shadow: 0 1.7rem 4.4rem black;
  z-index: 1; }
  .drawer .nav-box {
    height: inherit;
    top: 0;
    width: 100%; }
    .drawer .nav-box .list-items {
      height: 100%;
      display: flex;
      background-color: #150129;
      border-bottom: 1px solid #FFF; }
    .drawer .nav-box .list-item {
      flex: 1;
      list-style-type: none;
      position: relative;
      font-size: 2rem;
      transition: all .4s;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 2rem;
      font-family: fantasy;
      background-size: 300%;
      width: 100%;
      height: 100%; }
      .drawer .nav-box .list-item:not(:nth-child(3)) {
        border-right: 1px solid #27094a; }
      .drawer .nav-box .list-item:hover {
        z-index: 1000;
        cursor: pointer;
        transition: all .5s .5s;
        flex: 2;
        box-shadow: 0 0 1rem #0f0e18; }
        .drawer .nav-box .list-item:hover > * > * {
          filter: invert(0);
          height: 6.8rem;
          width: 6.8rem;
          transition: all .26s .6s; }
      .drawer .nav-box .list-item::before {
        content: '';
        z-index: -1000;
        position: absolute;
        background-size: 300%;
        width: 100%;
        height: 0;
        transition: all .3s; }
      .drawer .nav-box .list-item:hover:before {
        background-color: #fff;
        box-shadow: 0 0 5rem rgba(0, 0, 0, 0.8); }
      .drawer .nav-box .list-item:not(:nth-child(2)):hover:before {
        top: 0;
        left: 0;
        height: 100%;
        transition: all .4s .3s;
        transform: scale(1) skewY(0); }
      .drawer .nav-box .list-item:nth-child(2):hover:before {
        bottom: 0;
        left: 0;
        height: 100%;
        transition: all .4s .3s;
        transform: scale(1) skewY(0); }
      .drawer .nav-box .list-item > * {
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        color: #222; }
        .drawer .nav-box .list-item > * > * {
          filter: invert(1);
          animation: filterIcons 1s  forwards infinite;
          width: 4rem;
          height: 4rem; }

@keyframes filterIcons {
  0% {
    transform: rotate(0); }
  25% {
    transform: rotate(10deg); }
  50% {
    transform: rotate(-10deg); }
  75% {
    transform: rotate(10deg); }
  100% {
    transform: rotate(0deg); } }

.main .study {
  height: calc(100vh - 8.1rem);
  padding: 2rem 3rem;
  width: 100%; }
  .main .study .grid {
    height: 100%; }
    .main .study .grid-layout {
      height: inherit;
      display: grid;
      grid-template-rows: repeat(3, 1fr);
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 3rem 2rem; }
      .main .study .grid-layout .item {
        font-size: 2rem;
        letter-spacing: 1px;
        overflow-x: visible;
        transition: all .4s .48s;
        background-color: rgba(0, 0, 0, 0.07);
        border: 1px solid rgba(255, 255, 255, 0.3);
        border-radius: 3px;
        position: relative;
        text-shadow: 0 0 1px white;
        z-index: 1000; }
        .main .study .grid-layout .item:hover {
          cursor: pointer;
          transform: scale(0.96); }
        .main .study .grid-layout .item::before {
          content: "";
          position: absolute;
          height: 100%;
          width: 0;
          background-color: rgba(0, 194, 255, 0.04);
          transition: all .5s;
          z-index: -1000; }
        .main .study .grid-layout .item:nth-child(odd):hover::before {
          right: 0;
          bottom: 0;
          width: 100%; }
        .main .study .grid-layout .item:nth-child(even):hover::before {
          top: 0;
          bottom: 0;
          width: 100%; }
        .main .study .grid-layout .item > *, .main .study .grid-layout .item > * > * {
          height: 100%;
          width: 100%;
          letter-spacing: 2px;
          color: rgba(61, 3, 126, 0.4);
          font-family: cursive;
          display: flex;
          font-size: 2rem;
          justify-content: center;
          align-items: center;
          transition: all .3s; }
          .main .study .grid-layout .item > *:hover, .main .study .grid-layout .item > * > *:hover {
            letter-spacing: 1px;
            color: #fff;
            animation: chaptersContent 2s forwards infinite; }

@keyframes chaptersContent {
  0% {
    transform: rotate(0); }
  25% {
    transform: rotate(3deg); }
  50% {
    transform: rotate(-3deg); }
  75% {
    transform: rotate(3deg); }
  100% {
    transform: rotate(0); } }

.chapter > .cards {
  padding: 0 1rem;
  max-width: 80rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  grid-gap: 2rem 4rem; }

.chapter .approval-card {
  margin: 0 auto;
  width: 100%;
  min-height: 8rem;
  background-color: transparent;
  border-radius: 8px;
  border: 2px solid #fff;
  display: flex;
  flex-direction: column; }
  .chapter .approval-card > *:not(.btns) {
    flex: 1; }
  .chapter .approval-card .btns {
    display: flex; }
    .chapter .approval-card .btns > * {
      font-size: 1.2rem;
      flex: 1;
      transition: all .1s; }
      .chapter .approval-card .btns > *:hover {
        transform: scale(1.02); }

.editor .editor-provider {
  width: 100%; }
  .editor .editor-provider .live-editor {
    min-height: 34rem;
    max-height: 50rem;
    box-shadow: inset 0 0 9rem #000;
    border-radius: 9px;
    transition: all .2s;
    font-size: 1.9rem;
    overflow: auto;
    transition: all .2s;
    padding: 4rem 5px;
    margin: 0 auto;
    max-width: 80vw;
    margin-bottom: 3rem;
    position: relative;
    background-color: #0c021b; }
    .editor .editor-provider .live-editor:hover {
      transform: scale(0.98);
      transition: all .1s; }
    .editor .editor-provider .live-editor:focus {
      background-color: rgba(0, 0, 0, 0.78);
      transition: all .2s; }
  .editor .editor-provider .who {
    max-height: 50vh;
    overflow: auto;
    transition: all .2s;
    border: 1px solid #fff;
    max-width: 80vw;
    margin: 0 auto; }
    .editor .editor-provider .who:hover {
      transform: scale(0.98);
      box-shadow: inset 0 0 5rem rgba(0, 0, 0, 0.8); }

.editor-error {
  color: #f52828;
  font-size: 2rem;
  padding: 1rem 0;
  text-align: center; }

.preview {
  max-height: 20rem; }

.main {
  height: calc(100vh - 1px);
  width: calc(100vw - 4rem);
  margin: 0 auto;
  background-color: #150129;
  box-shadow: 0 0 4rem black;
  overflow: auto;
  z-index: 0; }
  .main > div {
    padding: 4rem 0;
    z-index: 0;
    box-shadow: 0 0 -4rem black; }
  .main > div, .main > * {
    opacity: 1;
    transform: scale(1);
    animation-name: routePage;
    animation-duration: 1s; }

@keyframes routePage {
  0% {
    opacity: 0;
    transform: scale(0); }
  50% {
    opacity: 0.3;
    transform: scale(0.4); }
  60% {
    opacity: 0.2;
    transform: scale(0.3); }
  100% {
    transform: scale(1);
    opacity: 1; } }

/*    Layout    */
